import * as THREE from "three";
import { EffectComposer } from "three/examples/jsm/postprocessing/EffectComposer";
import { RenderPass } from "three/examples/jsm/postprocessing/RenderPass";
import { ShaderPass } from "three/examples/jsm/postprocessing/ShaderPass";
const vertexShader = `varying vec3 vNormal;
void main()
{
vNormal = normalize( normalMatrix * normal );
gl_Position = projectionMatrix * modelViewMatrix * vec4( position, 1.0 );
}`;
const fragmentShader = `varying vec3 vNormal;
void main()
{
float intensity = pow( 0.7 - dot( vNormal, vec3( 0.0, 0.0, 1.0 ) ), 4.0 );
gl_FragColor = vec4( 0.3, 0.4, 0.5, 1.0 ) * intensity;
}
`;
const PixelShader = {
    uniforms: {
        tDiffuse: { value: null },
        resolution: { value: null },
        pixelSize: { value: 8 },
    },
    vertexShader: [
        "constying highp vec2 vUv;",
        "void main() {",
        "vUv = uv;",
        "gl_Position = projectionMatrix * modelViewMatrix * vec4( position, 1.0 );",
        "}",
    ].join("\n"),
    fragmentShader: [
        "uniform sampler2D tDiffuse;",
        "uniform float pixelSize;",
        "uniform vec2 resolution;",
        "constying highp vec2 vUv;",
        "void main(){",
        "vec2 dxy = pixelSize / resolution;",
        "vec2 coord = dxy * floor( vUv / dxy );",
        "gl_FragColor = texture2D(tDiffuse, coord);",
        "}",
    ].join("\n"),
};
export const runBurnTheEarthGame = (id) => {
    // BASIC SETUP
    const scene = new THREE.Scene();
    const camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);
    camera.position.z = 2;
    const renderer = new THREE.WebGLRenderer({
        antialias: true,
    });
    // RENDERER SETUP
    renderer.setClearColor("#010001");
    renderer.setSize(window.innerWidth, window.innerHeight);
    renderer.setPixelRatio(window.devicePixelRatio ? window.devicePixelRatio : 1);
    const canvas = document.getElementById(id).appendChild(renderer.domElement);
    canvas.id = "burn-earth-canvas";
    window.addEventListener("resize", () => {
        renderer.setSize(window.innerWidth, window.innerHeight);
        camera.aspect = window.innerWidth / window.innerHeight;
        camera.updateProjectionMatrix();
    });
    // EARTH MATERIAL
    const createEarthMaterial = () => {
        const earthTexture = new THREE.Texture();
        const loader = new THREE.ImageLoader();
        loader.load("2k_earth_nightmap-min.jpg", (image) => {
            earthTexture.image = image;
            earthTexture.needsUpdate = true;
            earthTexture.anisotropy = renderer.capabilities.getMaxAnisotropy();
        });
        const earthMaterial = new THREE.MeshPhongMaterial();
        earthMaterial.map = earthTexture;
        earthMaterial.bumpMap = createBump();
        earthMaterial.bumpScale = 1;
        earthMaterial.map.minFilter = THREE.LinearFilter;
        return earthMaterial;
    };
    const createBump = () => {
        const earthBump = new THREE.Texture();
        const loader = new THREE.ImageLoader();
        loader.load("2k_earth_nightmap-min.jpg", (image) => {
            earthBump.image = image;
            earthBump.needsUpdate = true;
            earthBump.anisotropy = renderer.capabilities.getMaxAnisotropy();
        });
        return earthBump;
    };
    // MESH CREATION AND INSERT
    const geometry = new THREE.SphereGeometry(1, 200, 200);
    const mesh = new THREE.Mesh(geometry, createEarthMaterial());
    scene.add(mesh);
    // HALO MATERIAL
    const customMaterial = new THREE.ShaderMaterial({
        uniforms: {},
        vertexShader,
        fragmentShader,
        side: THREE.BackSide,
        blending: THREE.AdditiveBlending,
        transparent: true,
    });
    // HALO CREATION AND INSERT
    const haloGeometry = new THREE.SphereGeometry(1.45, 150, 150);
    const halo = new THREE.Mesh(haloGeometry, customMaterial);
    scene.add(halo);
    // CLOUDS
    const createCloudsMaterial = () => {
        const cloudsTexture = new THREE.Texture();
        const loader = new THREE.ImageLoader();
        loader.load("Earth-clouds.png", (image) => {
            cloudsTexture.image = image;
            cloudsTexture.needsUpdate = true;
            cloudsTexture.anisotropy = renderer.capabilities.getMaxAnisotropy();
        });
        const cloudsMaterial = new THREE.MeshPhongMaterial();
        cloudsMaterial.map = cloudsTexture;
        cloudsMaterial.map.minFilter = THREE.LinearFilter;
        cloudsMaterial.transparent = true;
        cloudsMaterial.opacity = 0.5;
        cloudsMaterial.alphaMap = createCloudsOpacity();
        return cloudsMaterial;
    };
    const createCloudsOpacity = () => {
        const cloudsTexture = new THREE.Texture();
        const loader = new THREE.ImageLoader();
        loader.load("Earth-clouds.jpg", (image) => {
            cloudsTexture.image = image;
            cloudsTexture.needsUpdate = true;
            cloudsTexture.anisotropy = renderer.capabilities.getMaxAnisotropy();
        });
        return cloudsTexture;
    };
    const clouds = new THREE.Mesh(new THREE.SphereGeometry(1.01, 32, 32), createCloudsMaterial());
    scene.add(clouds);
    // AUDIO
    const audioCtx = new AudioContext();
    let audioSource;
    const analyser = audioCtx.createAnalyser();
    const frequencyData = new Uint8Array(analyser.frequencyBinCount);
    const audio = new Audio();
    audio.crossOrigin = "anonymous";
    audio.controls = false;
    audio.src = "the-epic-2-by-rafael-krux.mp3";
    audio.loop = true;
    document.body.appendChild(audio);
    // Once the song is playable, the loader disappears and the init function start
    audio.addEventListener("canplay", () => {
        audioSource = audioCtx.createMediaElementSource(audio);
        audioSource.connect(analyser);
        analyser.connect(audioCtx.destination);
        audio.play();
    });
    // EARTH ANIMATION
    const update = () => {
        analyser.getByteFrequencyData(frequencyData);
        const frequencyIdx = Math.floor(50);
        const size = 1 + frequencyData[frequencyIdx] / frequencyData.length;
        mesh.scale.x = size;
        mesh.scale.y = size;
        mesh.scale.z = size;
        clouds.scale.x = size;
        clouds.scale.y = size;
        clouds.scale.z = size;
        halo.scale.x = size;
        halo.scale.y = size;
        halo.scale.z = size;
    };
    // LIGHT
    const light = new THREE.AmbientLight(0xffffff);
    light.position.set(10, 0, 25);
    scene.add(light);
    // ROTATION ON MOUSEMOVE
    window.onmousemove = (event) => {
        mesh.rotation.y -= event.movementX / 500;
    };
    // POSTPROCESSING
    let composer = new EffectComposer(renderer);
    composer.addPass(new RenderPass(scene, camera));
    let pixelPass = new ShaderPass(PixelShader);
    pixelPass.uniforms.resolution.value = new THREE.Vector2(window.innerWidth, window.innerHeight);
    pixelPass.uniforms.resolution.value.multiplyScalar(window.devicePixelRatio);
    composer.addPass(pixelPass);
    // RENDERING
    const animate = () => {
        // sphere.rotation.x += 0.01;
        // sphere.rotation.y += 0.01;
        mesh.rotation.y += 0.001;
        clouds.rotation.y += 0.0005;
        update();
        /* render scene and camera */
        renderer.render(scene, camera);
        requestAnimationFrame(animate);
    };
    requestAnimationFrame(animate);
};



