import styled from "styled-components";
export const StartGameInterface = styled.div `
  position: fixed;
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #000000aa;
`;
export const StartGameButton = styled.button `
  color: red;
`;
export const EarthSurface = styled.div `
  background: transparent;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 90vh;
  width: 90vh;
  border-radius: 100%;
`;
export const ProjectWrapper = styled.div `
  width: 100%;
  height: 100%;
  overflow: hidden;
`;
export const Project = styled.div ``;



