import Slider from "react-slick";
import styled from "styled-components";
export const SlidersContainer = styled.div `
  position: relative;
  width: 100vw;
  height: 100vh;
`;
export const StyledSlider = styled(Slider) `
  width: 100%;
  height: 100%;
`;
export const NavSliderContainer = styled.nav `
  position: fixed;
  bottom: 0;
  width: 100%;
  // background: linear-gradient(to bottom, transparent 0%, #000000cc 100%);
  // height: 350px;
  z-index: 10;
`;
export const NavSlider = styled(Slider) `
  position: fixed;
  bottom: 25px;
  left: 50%;
  transform: translateX(-50%);
  width: 85vw;
`;
export const NavElement = styled.div ``;
export const ProjectGifContainer = styled.div `
  height: 100px;
  display: flex;
  justify-content: center;
`;
export const ProjectGif = styled.img ``;



